<!--vip-->

<template>
  <div class="website-mccount-management">
    <Search :searchData="searchData" @response="onSubmit"></Search>
    <DataTable :dataTable="tableData" @modify="postModify" @delete="postDelete"></DataTable>
    <el-pagination background layout="total, prev, pager, next" :page-size="pageSize" @current-change="getPage" :total="total"></el-pagination>
  </div>
</template>

<script type="text/ecmascript-6">
  import Search from '../../components/Search.vue'
  import DataTable from '../../components/DataTableImg.vue'
  export default {
    name: 'vip-coctor-management',
    data() {
      return {
        searchData: {
          path: '/vipdm/newdoctorinformation',
          url: '',
          placeholder:''
        },
        total: 0,
        pageSize: 0,
        webSitedata: {
          currentPage: 1,
          customer: ''
        },
        //table
        tableData: {
          listname: [
//            {field: 'customerId', name: '', width: '50'},
            {field: 'creatDate', name: '', width: '100'},
            {field: 'photoUrl', name: '', width: '100'},
            {field: 'customerName', name: ''},
            {field: 'price', name: ''},
            {field: 'phone', name: '',},
            {field: 'hospital', name: ''},
            {field: 'department', name: '',},
            {field: 'title', name: '',},
            {field: 'loginName', name: ''},
            {field: 'customer', name: '',},
            {field: 'status', name: '',},
          ],  //
          data: []
        }
      }
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(){
        let _this = this;
        this.$axios.get('/api/back/doctor/vip', {params: this.webSitedata}).then((response)=> {
          let datelist = response.data.data.list;
          datelist.forEach(function(item,i){
            datelist[i].customerName = unescape(item.customerName);
            datelist[i].creatDate = _this.$timeonversionC(item.creatDate);
            datelist[i].hospital = unescape(item.hospital);
            datelist[i].department = unescape(item.department);
            datelist[i].title = unescape(item.title);
            datelist[i].status = item.status == 0 ? "" : "";
            datelist[i].photoUrl =!!item.photoUrl ? _this.$api +"/images/" + item.photoUrl : _this.$api +'/images/customerPhoto/20180424/2018042422430000147261.png';
          })
          this.tableData.data = datelist;
          this.total = response.data.data.total;
          this.pageSize = Number(response.data.data.pageSize);
          this.webSitedata.currentPage = response.data.data.pageNum;
        }).catch(function (error) {
          console.log(error);
        })
      },
      //seach 
      onSubmit(tabletext) {
        this.webSitedata.customer = tabletext;
        this.getData()
      },
      //
      getPage(a){
        this.webSitedata.currentPage = a;
        this.getData()
      },
      //id
      postModify(row){
        console.log(row)
        this.$router.push({path: "/vipdm/revisedoctorinformmation", query: {customerId: row.customerId}})
      },
      postDelete(row){
        console.log(row.usersid)
        this.$confirm('VIP', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customer/delete', {customerId: row.customerId})
            .then((response) => {
              this.$message({
                type: 'success',
                message: '',
              });
              this.getData();
            }).catch(function (error) {
            console.log(error);
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
    },
    components: {
      Search,
      DataTable
    }
  }
</script>

<style lang="scss">
  .tableCss {
    box-sizing: border-box;
    padding: 20px;
  }

  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>



