'use strict';

import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


export const state = {
  username: null,
  truename: null,
  type: null
}

export const mutations = {
  //   
  set_username (state, name) {
    state.username = name;
  },
  //   
  delete_username (state){
    state.username = '';
  },
  //   
  set_truename (state, name){
    state.truename = name;
  },
  //  
  delete_truename (state){
    state.truename = '';
  },
  //   
  set_type (state, name){
    state.type = name;
  },
  //  
  delete_type (state){
    state.type = '';
  },


}

export const actions = {
  //
  signIn (context, text) {
    context.commit('set_username',text.username)
    context.commit('set_truename',text.truename)
    context.commit('set_type',text.type)
  },
  signOut (context){
    context.commit('delete_username')
    context.commit('delete_truename')
    context.commit('delete_type')
  }

}


export default new Vuex.Store({
  state,
  mutations,
  actions
})



