<template>
  <div class="new-account m20 fsz14">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/doctorm' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="get_form mt20">
      <div class="uplogo">
        <span class="title">:</span>
        <el-upload
          class="avatar-uploader mb18"
          v-loading="loading"
          :action="upimgUrl"
          name="imagePath"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :on-error="handleAvatarError"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>

      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.hospital"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.department"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.customerName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.passWord"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button plain @click="setGuest">{{guest == 1 ? '':''}}</el-button>
          <el-button plain @click="disableFn">{{status == 0 ? '':''}}</el-button>
          <el-button plain @click="upgrade">VIP</el-button>
        </el-form-item>
        <el-form-item>
          <el-button plain @click="prohibitReply">{{form.reply == 1 ? '' : ''}}</el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import qs from 'qs';
  export default {
    name: 'revise-doctor-management',
    data(){
      return {
        upimgUrl: this.$urlapi + '/back/customers/image',
        imageUrl: this.$api+"/images/" + this.photoUrl,
        loading:false,
        form: {},
        status: null,
        guest: null,
      }
    },
    watch: {},
    mounted(){
      console.log("000")
      this.getData();
    },
    methods: {
      getData(){
        let form = {};
        this.$axios.get('/api/back/customers/' + this.$route.query.customerId).then((response)=> {
          console.log(response)
          let data = response.data.data;
          this.imageUrl = this.$api+"/images/" + data.photoUrl,
            this.form = {
              customerId: data.customerId,
              photoUrl: data.photoUrl,  //
              customerName: data.customerName,  //
              phone: data.phone, //
              hospital: data.hospital, //
              department: data.department, //
              title: data.title, //
              passWord:data.passWord, // 
              reply: data.reply ? data.reply : 0,   //   1    0
            }
            this.status = data.status
            this.guest = data.guest
        }).catch(function (error) {
          console.log(error);
        });
        return
      },
      //
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        console.log(file)
        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        this.loading = true;
        return isJPG && isLt2M;
      },
      //
      handleAvatarSuccess(res, file) {
        if(res.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          this.imageUrl = this.$api+"/images/" + res.data;
          this.form.photoUrl = res.data;
        }else{
          this.$message({
            type: 'error',
            message: ""
          });
        }
        this.loading = false;
      },
//
      handleAvatarError(res, file){
        this.loading = false;
        this.$message({
          type: 'error',
          message: ""
        });
      },
      //
      setGuest(){

        let _this = this;
        let messageText = '';
        if (this.guest == 0) {
          messageText = '';
        } else {
          messageText = '';
        }
        this.$confirm(messageText, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/doctor/guest', {customerId: this.$route.query.customerId}).then((response) => {
              console.log(response)
              this.$message({
                type: 'success',
                message: ''
              });
              history.go(-1)
            }).catch(function (error) {
              console.log(error);
            });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },

      ///
      disableFn(){
        let _this = this;
        let messageText = [];
        if (this.status == 0) {
          messageText[0] = '';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customers/lock', {customerId: this.$route.query.customerId})
            .then((response) => {
              console.log(response)
              this.$message({
                type: 'success',
                message: messageText[1]
              });
              history.go(-1)
            })
            .catch(function (error) {
              console.log(error);
            });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      //
      setReset(){
        this.$confirm("", '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/reset', {customerId: this.$route.query.customerId}).then((response) => {
          console.log(response)
        this.$message({
          type: 'success',
          message: ""
        });
        history.go(-1)
      }).catch(function (error) {
          console.log(error);
        });

      }).catch(() => {
          this.$message({
          type: 'info',
          message: ''
        });
      })
      },
      //
      upgrade(){
        this.$confirm('VIP', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customers/vip', {customerId: this.$route.query.customerId})
            .then((response) => {
            console.log(response)
            this.$message({
              type: 'success',
              message: ""
            });
            history.go(-1)
          })
        .catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      //
      prohibitReply(){
        let _this = this;
        let messageText = [];
        if (this.form.reply == 0) {
          messageText[0] = '?';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customer/reply', {customerId: this.$route.query.customerId})
            .then((response) => {
              console.log(response)
              this.$message({
                type: 'success',
                message: messageText[1]
              });
              history.go(-1)
            })
            .catch(function (error) {
              console.log(error);
            });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      //
      onSubmit() {
        let fromdata = {
          customerId: this.form.customerId,
          photoUrl: this.form.photoUrl,
          customerName: this.form.customerName,
          phone: this.form.phone,
          hospital: this.form.hospital,
          department: this.form.department,
          title: this.form.title,
          passWord: this.form.passWord,
          price: 0,
        }
        if(fromdata.phone.length < 1){
          this.$alert("", "", {
            confirmButtonText: '',
          });
          return
        }
        this.$axios.post('/api/back/doctors', fromdata).then((response)=> {
          console.log(response)
        if(response.data.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          history.go(-1)
        }
        }).catch(function (error) {
            console.log(error);
          });
      }
    },
  }
</script>

<style lang="scss">
</style>



