<!-- -->
<template>
  <div class="new-announcement m20 fsz14">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/expertlh' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <div class="edit mt20">
      <el-form ref="form" :model="form" label-width="60px">
        <el-form-item label="">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
      </el-form>
      <div style="overflow: hidden">
        <div class="fl"></div>
        <el-upload
          class="avatar-uploader mb18 fl"
          :action="upimgUrl"
          v-loading="loading"
          name="imagePath"
          :show-file-list="false"
          :on-success="handleAvatarSuccessImg"
          :on-error="handleAvatarError"
          :before-upload="beforeAvatarUploadImg">
          <img v-if="form.imgUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>

      <el-upload
        class="upload-demo"
        action="/api/back/article/video"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :before-remove="beforeRemove"
        name="imagePath"
        :limit="1"
        :on-exceed="handleExceed"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
        :file-list="fileList">
        <el-button size="small" type="primary"></el-button>
        <span style="color: #F56C6C">MP4</span>
      </el-upload>
      <quill-editor class="mt20" ref="myTextEditor" v-model="form.content" :config="editorOption"></quill-editor>
    </div>


    <!--<div class="block mt20">-->
      <!--<span class="demonstration"></span>-->
      <!--<el-date-picker v-model="datatext" type="datetime" placeholder=""></el-date-picker>-->
    <!--</div>-->
    <el-form ref="form" class="mt20">
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="text/ecmascript-6">
  import { VueEditor } from 'vue2-editor'
  export default {
    name: 'new-expert-lecture-hall',
    data(){
      return {
        upimgUrl: this.$urlapi + '/back/article/image',
        datatext: '',
        loading:false,
        content: '',             // 
        editorOption: {},          // 
        fileList:[],
        imageUrl:'',
        form: {
          title: '',
          content:'',
          imgUrl:'',
        },
      }
    },
    watch: {
      content(val){
        console.log(val)
      }
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(){
        console.log(this.$route.query.articleId)
        this.$axios.get('/api/back/article/' + this.$route.query.articleId).then((response)=> {
          console.log(response)
          let data = response.data.data;
          this.form.title = data.title;
          this.form.content = data.content;
        this.imageUrl = this.$api + "/images/" + data.imgUrl
        this.form.imgUrl = data.imgUrl
        if(!!data.outUrl){
          this.fileList.push({
            name:'',
            outUrl: data.outUrl,
          })
        }

        }).catch(function (error) {
          console.log(error);
        });
      },
//
      beforeAvatarUploadImg(file) {
        console.log(file.type)
        const isJPG = file.type === 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.loading = false;
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.loading = false;
          this.$message.error(' 2MB!');
        }
        this.loading = true;
        return isJPG && isLt2M;
      },

      //
      handleAvatarSuccessImg(res, file) {
        if(res.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          this.imageUrl = this.$api + "/images/" + res.data;
          this.form.imgUrl = res.data;
        }else{
          this.$message({
            type: 'error',
            message: ""
          });
        }
        this.loading = false;

      },
      //
      handleAvatarError(res, file){
        this.loading = false;
        this.$message({
          type: 'error',
          message: ""
        });
      },
      //
      beforeAvatarUpload(file, fileList){
        console.log(file)
      },
      handleAvatarSuccess(file, fileList){
        if(file.errcode == 0 ){
          this.form.outUrl  = file.data
        }
      },
      handleRemove(file, fileList) {
        this.form.outUrl = ''
        console.log(file, fileList);
      },
      handlePreview(file) {
        window.open(this.$api +"/images/"+ file.outUrl);
        console.log(file.outUrl);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },

      //
      onSubmit(){
        let formdata = {
          articleId: this.$route.query.articleId,
          title:this.form.title,
          outUrl: this.form.outUrl,
          content:this.form.content,
          imgUrl:this.form.imgUrl,
          categoryId: 2,
          sortCode: 1,
        }
        this.$axios.post('/api/back/article', formdata).then((response) => {
          if(response.data.errcode === 0){
            this.$message({
              showClose: true,
              type: "success",
              message: ''
            });
            history.go(-1)
          }
        }).catch((error) => {
          console.log(error);
        });
      }
    },
    components: {
      VueEditor
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" type="text/css">
  .edit {
    width: 660px;
    height: auto;
  }

  .quill-editor {
    height: 300px;

    .ql-container {
      height: 234px;
    }
  }
  .ql-snow .ql-tooltip{
    position: absolute;
  }
  .ql-editor.ql-blank::before {
    content: "" !important;
  }

  .limit {
    height: 30px;
    border: 1px solid #ccc;
    line-height: 30px;
    text-align: right;
    span {
      color: #ee2a7b;
    }
  }

  .ql-snow .ql-editor img {
    max-width: 480px;
  }

  .ql-editor .ql-video {
    max-width: 480px;
  }
</style>



