<!-- -->
<template>
  <div class="new-announcement m20 fsz14">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/expertlh' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="edit mt20">
      <div class="edit mt20">
        <el-form ref="form" :model="form" label-width="60px">
          <el-form-item label="">
            <el-input v-model="form.title" :maxlength="maxNumber" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div style="overflow: hidden">
        <div class="fl"></div>

        <el-upload
          class="avatar-uploader mb18 fl"
          v-loading="loading"
          :action="upimgUrl"
          name="imagePath"
          :show-file-list="false"
          :on-success="handleAvatarSuccessImg"
          :on-error="handleAvatarError"
          :before-upload="beforeAvatarUploadImg">
          <img v-if="form.imgUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <span style="color: #cccccc"></span>
      </div>


      <el-upload
        class="upload-demo"
        :action="upimgUrl"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :before-remove="beforeRemove"
        name="imagePath"
        :limit="1"
        :on-exceed="handleExceed"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload"
        :file-list="fileList">
        <el-button size="small" type="primary"></el-button>
        <span style="color: #cccccc"></span>
        <span style="color: #F56C6C">MP4</span>
      </el-upload>

      <quill-editor class="mt20" ref="myTextEditor" v-model="form.content" :config="editorOption"></quill-editor>
      <span style="color: #cccccc">15M</span>
    </div>
    <el-form ref="form" class="mt20">
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script type="text/ecmascript-6">
  import { VueEditor } from 'vue2-editor'
  export default {
    name: 'new-expert-lecture-hall',
    data(){
      return {
        maxNumber: 100,
        upimgUrl: this.$urlapi + '/back/article/image',
        editorOption: {},          // 
        fileList: [],
        datatext:'',
        loading: false,
        imageUrl:'',
        form: {
          title: '',
          content: '',             // 
          outUrl:'',
          imgUrl:'',
        },
      }
    },
    watch: {
      content(val){
        console.log(val)
      }
    },
    mounted(){

    },
    methods: {
      //
      beforeAvatarUploadImg(file) {
        console.log(file.type)
        const isJPG = file.type === 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.loading = false;
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.loading = false;
          this.$message.error(' 2MB!');
        }
        this.loading = true;
        return isJPG && isLt2M;
      },

      //
      handleAvatarSuccessImg(res, file) {
        if(res.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          this.imageUrl = this.$api + "/images/" + res.data;
          this.form.imgUrl = res.data;
        }else{
          this.$message({
            type: 'error',
            message: ""
          });
        }
        this.loading = false;
      },
      //
      handleAvatarError(res, file){
        this.loading = false;
        this.$message({
          type: 'error',
          message: ""
        });
      },
      beforeAvatarUpload(file, fileList){
        console.log(file)
      },
      handleAvatarSuccess(file, fileList){
        if(file.errcode == 0 ){
          this.form.outUrl  = file.data
        }
      },
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
      },
      beforeRemove(file, fileList) {
        return this.$confirm(` ${ file.name }`);
      },
      //
      onSubmit(){
        if(this.form.title.length <= 0){
          this.$message({
            type: 'error',
            message: ""
          });
          return
        }

        if(this.form.imgUrl.length <= 0){
          this.$message({
            type: 'error',
            message: ""
          });
          return
        }

        if(this.form.outUrl.length <= 0){
          this.$message({
            type: 'error',
            message: ""
          });
          return
        }
        let formdata = {
          title:this.form.title,
          outUrl: this.form.outUrl,
          content:this.form.content,
          truename: this.cookieFn.get("truename"),
          imgUrl:this.form.imgUrl,
          categoryId: 2,   //
          sortCode: 1,
        }


        this.$axios.post('/api/back/article', formdata).then((response) => {

          if(response.data.errcode === 30004){
            this.$message({
              showClose: true,
              type: "error",
              message: ''
            });
          return
          }

        if(response.data.errcode === 0){
          this.$message({
            showClose: true,
            type: "success",
            message: ''
          });
          history.go(-1)
        }
        }).catch((error) => {
          console.log(error);
        });
      }
    },
    components: {
      VueEditor
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" type="text/css">
  .edit {
    width: 660px;
    height: auto;
  }

  .quill-editor {
    height: 300px;

    .ql-container {
      height: 234px;
    }
  }
  .ql-snow .ql-tooltip{
    position: absolute;
  }
  .ql-editor.ql-blank::before {
    content: "" !important;
  }

  .limit {
    height: 30px;
    border: 1px solid #ccc;
    line-height: 30px;
    text-align: right;
    span {
      color: #ee2a7b;
    }
  }

  .ql-snow .ql-editor img {
    max-width: 480px;
  }

  .ql-editor .ql-video {
    max-width: 480px;
  }
</style>



