<template>
  <div class="new-account m20 fsz14">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/coctorm' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="get_form mt20">
      <div class="uplogo">
        <span class="title">:</span>
        <el-upload
          class="avatar-uploader mb18"
          v-loading="loading"
          :action="upimgUrl"
          name="imagePath"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :on-error="handleAvatarError"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>

      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="" prop="hospital" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.hospital"></el-input>
        </el-form-item>
        <el-form-item label="" prop="department" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.department"></el-input>
        </el-form-item>
        <el-form-item label="" prop="title" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="customerName" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.customerName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit('form')"></el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import qs from 'qs';
  export default {
    name: 'new-doctor-management',
    data(){
      return {
        upimgUrl: this.$urlapi + '/back/customers/image',
        imageUrl: '',
        loading:false,
        form: {
          photoUrl: '',  //
          customerName: '',  //
          phone:'', //
          hospital:'', //
          department:'', //
          title:'', //
          type: 0
        }
      }
    },
    watch: {},
    methods: {
      //
      handleAvatarSuccess(res, file) {
        if(res.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          this.imageUrl = this.$api + "/images/" + res.data;
          this.form.photoUrl = res.data;
        }else{
          this.$message({
            type: 'error',
            message: ""
          });
        }
        this.loading = false;
      },
      //
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        console.log(file)
        if (!isJPG) {
          this.loading = false;
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.loading = false;
          this.$message.error(' 2MB!');
        }
        this.loading = true;
        return isJPG && isLt2M;
      },
      //
      handleAvatarError(res, file){
        this.loading = false;
        this.$message({
          type: 'error',
          message: ""
        });
      },
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          console.log("=====")
          if (!valid) {
            throw new Error(''); //
          }
        });

        let fromdata = {
          photoUrl: this.form.photoUrl,
          customerName: escape(this.form.customerName),
          phone: this.form.phone,
          hospital:escape(this.form.hospital),
          department:escape(this.form.department),
          title:escape(this.form.title),
          price: '0',
          customersId:'',
          type: this.form.type,
        }
        if(fromdata.phone.length != 11){
          this.$message({
            type: 'error',
            message: ""
          });
         return
        }
        this.$axios.post('/api/back/doctors', fromdata).then((response)=> {
            console.log(response)
        if(response.data.errcode == 30012){
          this.$message({
            showClose: true,
            type: "error",
            message: ''
          });
          return
        }
        if(response.data.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          history.go(-1)
        }
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    mounted(){

    }
  }
</script>

<style lang="scss">

</style>



