<template>
  <div class="new-account m20 fsz14">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/websitemm' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="get_form mt20">
      <div class="uplogo">
        <span class="title">logo:</span>
        <el-upload
          class="avatar-uploader mb18"
          v-loading="loading"
          :action="upimgUrl"
          name="imagePath"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :on-error="handleAvatarError"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>

      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="" prop="truename" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.truename"></el-input>
        </el-form-item>
        <el-form-item label="" prop="linkName" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.linkName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-input v-model="form.phone" ></el-input>
        </el-form-item>

        <el-form-item>
          <a href="javascript:void(0);" @click="disableFn">{{form.lockState == 0 ? '':''}}</a>
          <!--<a href="javascript:void(0);" class="ml20" @click="resetFn"></a>-->
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit('form')"></el-button>
        </el-form-item>
      </el-form>

    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import qs from 'qs';
  export default {
    name: 'new-account',
    data(){
      return {
        upimgUrl: this.$urlapi + '/back/users/image',
        imageUrl: "",
        loading: false,
        form: {
          photoUrl: "",
          truename: "",
          linkName: "",
          phone: "",
          lockState: '',
        }
      }
    },
    mounted(){
      this.$axios.get('/api/back/users/' + this.$route.query.usersid, {params: {usersid: this.$route.query.usersid}}).then((response)=> {
        console.log(response)
        let data = response.data.data;
        this.imageUrl = this.$api+ "/images/" + data.photoUrl, this.form.photoUrl = data.photoUrl;
        this.form.truename = data.truename;
        this.form.linkName = data.linkName;
        this.form.phone = data.phone;
        this.form.lockState = data.lockState;
        console.log(this.getdata)
      }).catch(function (error) {
        console.log(error);
      });
    },
    methods: {
      //
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG) {
          this.loading = false;
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.loading = false;
          this.$message.error(' 2MB!');
        }
        this.loading = true;
        return isJPG && isLt2M;
      },
      //
      handleAvatarSuccess(res, file) {
        if(res.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          this.imageUrl = this.$api + "/images/" + res.data;
          this.form.photoUrl = res.data;
        }else{
          this.$message({
            type: 'error',
            message: ""
          });
        }
        this.loading = false;

      },

      //
      handleAvatarError(res, file){
        this.loading = false;
        this.$message({
          type: 'error',
          message: ""
        });
      },
      //
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          console.log("=====")
        if (!valid) {
          throw new Error(''); //
        }
      });
        let form = {
          usersid: this.$route.query.usersid,
          photoUrl: this.form.photoUrl,
          truename: escape(this.form.truename),
          linkName: escape(this.form.linkName),
          phone: this.form.phone
        }
        this.$axios.post('/api/back/users/webSite', form)
          .then((response)=> {
            this.$message({
              showClose: true,
              type: "success",
              message: ''
            });
            history.go(-1)
          })
          .catch(function (error) {
          this.$message({
            showClose: true,
            type: "error",
            message: ''
          });
            console.log(error);
          });
      },

      //
      disableFn() {
        let _this = this;
        let messageText = [];
        if (this.form.lockState == 0) {
          messageText[0] = '';
          messageText[1] = '';
        }else{
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/users', {usersid: this.$route.query.usersid})
            .then((response)=> {
              console.log(response)
              this.$message({
                type: 'success',
                message: messageText[1],
              });

              history.go(-1)
            })
            .catch(function (error) {
              console.log(error);
            });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      //  
      resetFn() {
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          //
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    },
  }
</script>

<style lang="scss">

</style>



