<template>
  <div class="new-account m20 fsz14 por h100">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/roleam' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/roleam/roleamlist', query: { usersid: form.parentUsersId }}">
        
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="get_form mt20">
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.truename"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="">
          <div>
            <template v-for="name in powerTag">
              <el-tag class="mr10">{{name}}</el-tag>
            </template>
          </div>
        </el-form-item>

        <el-form-item>
          <a href="javascript:void(0);" @click="purviewidsAlert"></a>
          <a href="javascript:void(0);" class="ml20" @click="disableFn">{{form.lockState ==0 ? '':''}}</a>
          <!--<a href="javascropt:;" class="ml20" @click="resetFn"></a>-->
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>

    </div>
    <template>
      <div class="alert_box_transfer" v-show="purviewidsstate">
        <h3></h3>
        <el-transfer v-model="purviewids" :button-texts="['', '']" :titles="['', '']" :data="powerdata">
        </el-transfer>
        <el-button class="preservation_but" type="primary" @click="preservationFn"></el-button>
        <el-button class="preservation_but mr20" @click="purviewidsAlert"></el-button>
      </div>
    </template>
  </div>
</template>

<script type="text/ecmascript-6">
  import qs from 'qs';
  export default {
    name: 'new-account',
    data(){
      const generateData = () => {
        const data = [];
        this.$axios.get('/api/back/purview')
          .then((response) => {
            let datalist = response.data.data;
            datalist.forEach(function (item, i) {
              if (item.isleaf === 1) {
                data.push({
                  key: item.purviewid,
                  label: item.purviewname,
                  disabled: true
                });
                item.childPurview.forEach(function (tem, i) {
                  data.push({
                    key: tem.purviewid,
                    label: tem.purviewname,
                    disabled: false
                  });
                })
              } else if (item.isleaf === 0) {
                data.push({
                  key: item.purviewid,
                  label: item.purviewname,
                  disabled: false
                });
              }
            })
          })
          .catch((error) => {
            console.log(error);
          })
        console.log(data)
        return data;
      }
      return {
        powerdata: generateData(), //
        purviewids: [],        //id
        purviewidsstate: false, //
        powerTag: [],
        truename: this.cookieFn.get("truename"),
        form: {
          parentUsersId: this.$route.query.parentUsersId,  //id
          usersid: this.$route.query.usersid,     //id
          truename: '',  //
          username: '',   //
          password: '',   //
          lockState: '',    //
        }
      }
    },
    watch: {
      purviewids(val){
        console.log(val)
      }
    },
    mounted(){
      this.getData();
    },
    methods: {
//      
      getData(){
        //
        this.$axios.get('/api/back/users/' + this.form.usersid, {params: {usersid: this.form.usersid}})
          .then((response) => {
            console.log(response)
            let data = response.data.data;
            this.form.truename = decodeURIComponent(data.truename);
            this.form.username = data.username;
            this.form.lockState = data.lockState;
            this.form.password = data.password
            this.powerShow()
          })
          .catch(function (error) {
            console.log(error);
          });
        //
        this.$axios.get('/api/back/purview/' + this.form.usersid)
          .then((response) => {
            console.log("")
            console.log(response)
            let data = response.data.data;
            data.forEach((item, i) => {
              this.purviewids.push(item.purviewid)
            })
            this.powerShow();
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      //
      purviewidsAlert(){
        this.purviewidsstate = !this.purviewidsstate;
      },
      //
      preservationFn(){
        this.purviewidsAlert();
        this.powerShow();
      },
      //
      powerShow(){
        let _this = this;
        this.powerTag = [];
        this.powerdata.forEach(function (item, i) {
          _this.purviewids.forEach(function (tem, s) {
            if (item.key === tem) {
              _this.powerTag.push(item.label)
            }
          })
        })
      },
      //
      onSubmit() {
        //
        let fromdata = {
          usersid: this.form.usersid,
          parentUsersId: this.form.parentUsersId,
          truename: escape(this.form.truename),
          username: this.form.username,
          password: this.form.password,
          type: 2,
        }

        // 
        let setPurview = {
          purviewids: this.purviewids.join(","),
          usersid: this.form.usersid
        }
        this.$axios.post('/api/back/purview', qs.stringify(setPurview), {
          headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'}
        }).then((response) => {
          //
          if (response.data.errcode === 0) {
            this.$axios.post('/api/back/users/role', fromdata).then((response) => {
              console.log(response)
              this.$message({
                type: 'success',
                message: ""
              });
              history.go(-1)
            }).catch(function (error) {
              console.log(error);
            });
          }
        }).catch(function (error) {
          console.log(error);
        });


      },
      //
      disableFn() {
        let _this = this;
        let messageText = [];
        if (this.form.lockState == 0) {
          messageText[0] = '';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/users', {usersid: this.$route.query.usersid})
            .then((response) => {
              console.log(response)
              this.$message({
                type: 'success',
                message: messageText[1]
              });
              history.go(-1)
            })
            .catch(function (error) {
              console.log(error);
            });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      //
      resetFn(){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          //  
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    },
  }
</script>

<style lang="scss">
  .alert_box_transfer {
    background: #eee;
    border: 1px #bbb solid;
    display: block;
    width: 530px !important;
    padding: 30px;
    position: absolute;
    border-radius: 4px;
    top: 34%;
    left: 50%;
    transform: translate(-50%, -50%);

  .preservation_but {
    float: right;
    margin-top: 20px;
  }

  }
</style>



