<!--

1.
     2.
  searchData    object
           {
             path
             url:  
           }
            "response"
<Search :searchData="" @response=""> </Search>
-->

<template>
	<div class="searchBox">
		<el-form :inline="true" class="demo-form-inline mt10">
			<el-button class="fl ml10" type="primary" @click="newAccount">{{searchData.newname ? searchData.newname : ''}}</el-button>
      <el-form-item class="fr">
        <el-button icon="el-icon-search" type="primary" @click="onSubmit"></el-button>
      </el-form-item>
			<el-form-item :label="searchData.name" class="fr">
				<el-input v-model="text" :placeholder="searchData.placeholder"></el-input>
			</el-form-item>
		</el-form>
	</div>
</template>

<script type="text/javascript">
	export default {
		name: 'searchBox',
    props: {
      searchData: {
        type: Object,      //objectl
        required: true,    //
      }
    },
    data() {
			return {
         path: this.searchData.path,
         url:this.searchData.url,
         text:''
			}
		},
		methods: {
			onSubmit() {    //   
        this.$emit('response', this.text);
			},
      newAccount(){    //
			  console.log(this)
        this.$router.push({path: this.path})
      }
		}
	}
</script>

<style scoped lang="scss">

</style>



