<!---->
<template>
  <div class="enterpris_announcement m20">
    <!---->
      <Search :searchData="searchData" @response="onSubmit"></Search>
    <!--table-->
    <el-table class="mt30" :data="tableData.data" height="400" border stripe style="width: 100%"
              :row-class-name="tableRowClassName">
      <!--<el-table-column align="center" prop="articleId" label=""  width="120"></el-table-column>-->
      <el-table-column align="center" prop="creatDate" label="" width="90"></el-table-column>
      <el-table-column align="center" prop="title" label=""></el-table-column>
      <el-table-column align="center" label="" width="240">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" v-show="scope.row.isPass == 0" @click="postnline(scope.row)">
          </el-button>
          <el-button size="mini" type="primary" v-show="scope.row.isPass == 1" @click="postOffline(scope.row)">
          </el-button>
          <el-button size="mini" type="primary" v-show="scope.row.isPass == 0" @click="postModify(scope.row)">
          </el-button>
          <el-button size="mini" type="danger" v-show="scope.row.isPass == 0" @click="postDalete(scope.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="modifyUser" label=""></el-table-column>
    </el-table>
    <!---->
    <el-pagination class="mt20" background layout="total, prev, pager, next" :page-size="pageSize" @current-change="getPage"
                   :total="total"></el-pagination>
  </div>
</template>

<script type="text/ecmascript-6">
  import Search from '../../components/Search.vue'
  export default {
    name: 'enterpris_announcement',
    data(){
      return {
        searchData: {
          path: '/enterprisa/newannouncement',
          name:'',
          placeholder:'',
          newname: '',
        },
        total: 0,
        pageSize: 0,
        webSitedata: {
          currentPage: 1,
          categoryId: 1,   //
          title:'',
        },
        tableData: {
          data: [],
        }

      }
    },
    mounted(){
      this.getData();
    },
    methods: {
      getData(){
        let _this = this;
        this.tableData.data = [];
        this.$axios.get('/api/back/article', {params: this.webSitedata}).then((response)=> {
          let datelist = response.data.data.list;
          datelist.forEach(function (item, i) {
            datelist[i].creatDate = _this.$timeonversionC(item.creatDate);
            datelist[i].truename = unescape(item.truename);
            datelist[i].linkName = unescape(item.linkName);
          })
          this.tableData.data = datelist;
          this.total = response.data.data.total;
          this.pageSize = Number(response.data.data.pageSize);
          this.webSitedata.currentPage = response.data.data.pageNum;
        }).catch(function (error) {
          console.log(error);
        })
      },
      //
      getPage(a){
        this.webSitedata.currentPage = a;
        this.getData();
      },
      //
      onSubmit(textdata){
        this.webSitedata.title = textdata
        this.getData()
      },
      //
      postnline(val){
        let formdata = {
          articleId: val.articleId
        }
        this.$axios.post('/api/back/article/pass', formdata).then((response) => {
          console.log(response)
          if (response.data.errcode == 0) {
            this.getData();
            this.$message({
              type: 'success',
              message: '!'
            });
          }else{
            this.$message({
              type: 'error',
              message: '!'
            });
          }
        }).catch((error) => {
          console.log(error);
        });
      },
      //
      postOffline(row){
        this.$axios.post('/api/back/article/pass', {articleId: row.articleId}).then((response) => {
          console.log(response)
          if (response.data.errcode == 0) {
            this.getData();
            this.$message({
              type: 'success',
              message: '!'
            });
          }else{
            this.$message({
              type: 'error',
              message: '!'
            });
          }
        }).catch((error) => {
          console.log(error);
        });
      },
      //
      postDalete(row){
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/article/delete', {articleId: row.articleId})
            .then((response) => {
              this.$message({
                type: 'success',
                message: '',
              });
              this.getData();
            }).catch(function (error) {
            console.log(error);
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },

      //
      postModify(row){
        this.$router.push({path: "/enterprisa/reviseannouncement", query: {articleId: row.articleId}})
      },


      newAccount(){
        this.$router.push({path: '/enterprisa/newannouncement'})
      },


      tableRowClassName({
        row,
        rowIndex
      }) {
        if (rowIndex === 1) {
          return 'warning-row';
        } else if (rowIndex === 3) {
          return 'success-row';
        }
        return '';
      },
    },
    components: {
      Search,
    }
  }
</script>

<style scoped lang="scss" rel="stylesheet/scss" type="text/css">

</style>



