<!--vip-->
<template>
  <div class="new-account m20 por fsz14">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/vipdm' }">VIP</el-breadcrumb-item>
      <el-breadcrumb-item>VIP</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="get_form mt20">
      <div class="uplogo">
        <span class="title">:</span>
        <el-upload
          class="avatar-uploader mb18"
          v-loading="loading"
          :action="upimgUrl"
          name="imagePath"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :on-error="handleAvatarError"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>

      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.hospital"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.department"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.customerName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.passWord"></el-input>
        </el-form-item>
        <el-form-item label="">
          <div>
            <template v-for="name in memberTag">
              <el-tag class="mr10">{{name}}</el-tag>
            </template>
          </div>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" plain @click="purviewidsAlert"></el-button>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.price">
            <template slot="append"></template>
          </el-input>
        </el-form-item>

        <el-form-item>
          <el-button plain @click="setGuest">{{guest == 1 ? '':''}}</el-button>
          <el-button plain @click="disableFn">{{status ==0 ? '':''}}</el-button>
          <el-button plain @click="upgrade"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button plain @click="prohibitReply">{{form.reply == 1 ? '' : ''}}</el-button>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <template>
      <div class="alert_box_transfer" v-show="memberidsstate">
        <h3></h3>
        <el-transfer v-model="memberids" :button-texts="['', '']" :titles="['', '']" :data="memberdata">
        </el-transfer>
        <el-button class="preservation_but" type="primary" @click="preservationFn"></el-button>
        <el-button class="preservation_but mr20" @click="purviewidsAlert"></el-button>
      </div>
    </template>
  </div>
</template>

<script type="text/ecmascript-6">
  import qs from 'qs';
  export default {
    name: 'revise-vip-coctor-management',
    data(){
      const generateData = () => {
        const data = [];
        this.$axios.get('/api/back/doctors/customers')
          .then((response) => {
            console.log(response)
            let datalist = response.data.data;
            datalist.forEach(function (item, i) {
              data.push({
                key: item.customerId,
                label: item.customerName,
              });
            })
          })
          .catch((error) => {
            console.log(error);
          })
        console.log(data)
        return data;
      }
      return {
        upimgUrl: this.$urlapi + '/back/customers/image',
        imageUrl: '',
        loading:false,
        form: {
          photoUrl: '',  //
          customerName: '',  //
          phone: '', //
          hospital: '', //
          department: '', //
          title: '', //
          price: '',   //
          customersId: '',  //ID
          passWord: '', //
          type: 1
        },
        status:'',
        guest:'',
        memberdata: generateData(), //
        memberids: [],        //id
        memberidsstate: false, //
        memberTag: [],
      }
    },
    watch: {},
    methods: {
      getData(){
        this.$axios.get('/api/back/customers/' + this.$route.query.customerId).then((response)=> {
          let data = response.data.data;
            this.imageUrl = this.$api+"/images/" + data.photoUrl,
            this.form = {
              photoUrl: data.photoUrl,  //
              customerName: unescape(data.customerName),  //
              phone: data.phone, //
              hospital: unescape(data.hospital), //
              department: unescape(data.department), //
              title: unescape(data.title), //
              passWord: data.passWord,
              price: data.price,
              customersId: data.customersId,
              reply: data.reply ? data.reply : 0,   //   1    0
            }
            this.status = data.status
            this.guest = data.guest
        }).catch(function (error) {
          console.log(error);
        });

        //
        let _this = this
        this.$axios.get('/api/back/doctors/customers?customerId=' + this.$route.query.customerId).then((response)=> {
          let data = response.data.data;
          data.forEach(function(tiem,i){
            _this.memberids.push(tiem.customerId)
            _this.memberTag.push(tiem.customerName)
            _this.memberdata.push({
              key: tiem.customerId,
              label: tiem.customerName,
            });
          })
        }).catch(function (error) {
          console.log(error);
        });


      },
      //
      handleAvatarSuccess(res, file) {
        if(res.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          this.imageUrl = this.$api + "/images/" + res.data;
          this.form.photoUrl = res.data;
        }else{
          this.$message({
            type: 'error',
            message: ""
          });
        }
        this.loading = false;
      },
      //
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg' || 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        console.log(file)
        if (!isJPG) {
          this.loading = false;
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.loading = false;
          this.$message.error(' 2MB!');
        }
        this.loading = true;
        return isJPG && isLt2M;
      },
      //
      handleAvatarError(res, file){
        this.loading = false;
        this.$message({
          type: 'error',
          message: ""
        });
      },
      //
      purviewidsAlert(){
        this.memberidsstate = !this.memberidsstate;
      },
      //
      preservationFn(){
        this.purviewidsAlert();
        this.powerShow();
      },
      //
      powerShow(){
        let _this = this;
        this.memberTag = [];
        this.memberdata.forEach(function (item, i) {
          _this.memberids.forEach(function (tem, s) {
            if (item.key === tem) {
              _this.memberTag.push(item.label)
            }
          })
        })
      },
      ///
      disableFn(){
        let _this = this;
        let messageText = [];
        if (this.status == 0) {
          messageText[0] = '';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customers/lock', {customerId: this.$route.query.customerId})
            .then((response) => {
              console.log(response)
              this.$message({
                type: 'success',
                message: messageText[1]
              });
              history.go(-1)
            })
            .catch(function (error) {
              console.log(error);
            });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      //
      setReset(){
        this.$confirm("", '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/reset', {customerId: this.$route.query.customerId}).then((response) => {
            console.log(response)
            this.$message({
              type: 'success',
              message: ""
            });
            history.go(-1)
          }).catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },

      //
      setGuest(){
        let _this = this;
        let messageText = '';
        if (this.guest == 0) {
          messageText = '';
        } else {
          messageText = '';
        }
        this.$confirm(messageText, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/doctor/guest', {customerId: this.$route.query.customerId}).then((response) => {
            console.log(response)
            this.$message({
              type: 'success',
              message: ''
            });
            history.go(-1)
          }).catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },

      //
      upgrade(){
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customers/vip', {customerId: this.$route.query.customerId})
            .then((response) => {
            console.log(response)
            this.$message({
              type: 'success',
              message: ""
            });
            history.go(-1)
          })
        .catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
//
      prohibitReply(){
        let _this = this;
        let messageText = [];
        if (this.form.reply == 0) {
          messageText[0] = '?';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customer/reply', {customerId: this.$route.query.customerId})
          .then((response) => {
          console.log(response)
        this.$message({
          type: 'success',
          message: messageText[1]
        });
        history.go(-1)
      })
      .catch(function (error) {
          console.log(error);
        });

      }).catch(() => {
          this.$message({
          type: 'info',
          message: ''
        });
      })
      },
      //
      onSubmit() {
        let formdata = {
          customerId: Number(this.$route.query.customerId),
          photoUrl: this.form.photoUrl,  //
          customerName: this.form.customerName,  //
          phone: this.form.phone, //
          hospital: this.form.hospital, //
          department: this.form.department, //
          title: this.form.title, //
          price: this.form.price,   //
          passWord: this.form.passWord,
          customersId: '',  //ID
        }
        if(this.memberids.length > 0){
          formdata.customersId = this.memberids.join(",");
        }

        if(formdata.phone.length < 1){
          this.$alert("", "", {
            confirmButtonText: '',
          });
          return
        }

        this.$axios.post('/api/back/doctors',formdata).then((response)=> {
            console.log(response)
        if(response.data.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          history.go(-1)
        }
        }).catch(function (error) {
            console.log(error);
          });
      }
    },
    mounted(){
      this.getData();
    }
  }
</script>

<style lang="scss">
  .alert_box_transfer {
    background: #eee;
    border: 1px #bbb solid;
    display: block;
    width: 524px;
    padding: 30px;
    position: absolute;
    border-radius: 4px;
    top: 34%;
    left: 50%;
    transform: translate(-50%, -50%);

  .preservation_but {
    float: right;
    margin-top: 20px;
  }

  }
</style>



