<!---->
<template>
  <div class="new-account m20 fsz14">
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '/regularmm' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="get_form mt20">
      <div class="uplogo">
        <span class="title">:</span>
        <el-upload
          class="avatar-uploader mb18"
          v-loading="loading"
          :action="upimgUrl"
          name="imagePath"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :on-error="handleAvatarError"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </div>

      <el-form ref="form" label-width="120px">
        <el-form-item label="">
          <el-input v-model="form.customerName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex" :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-select v-model="form.sex" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.passWord"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button plain @click="upgrade">VIP</el-button>
          <el-button plain @click="disableFn">{{form.status ==0 ? '':''}}</el-button>
          <el-button plain @click="prohibitRelease">{{form.releases == 1 ? '' : ''}}</el-button>

        </el-form-item>
        <el-form-item>
          <el-button plain @click="prohibitReply">{{form.reply == 1 ? '' : ''}}</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import qs from 'qs';
  export default {
    name: 'new-doctor-management',
    data(){
      return {
        loading:false,
        options:[{
          value: '',
          label: ''
        },{
          value: '',
          label: ''
        }],
        upimgUrl: this.$urlapi + '/back/customers/image',
        imageUrl: '',
        form: "",
      }
    },
    watch: {},
    mounted(){
      this.getForm()
    },
    methods: {
      getForm(){
      let form = {};
      this.$axios.get('/api/back/customers/' + this.$route.query.customerId).then((response)=> {
        console.log(response)
        let data = response.data.data;
         this.imageUrl = this.$api+"/images/" + data.photoUrl,
          this.form = {
            photoUrl: data.photoUrl,  //
            customerName: decodeURIComponent(data.customerName),  //
            phone: data.phone, //
            cardNo: data.cardNo, //
            status: data.status,
            passWord: data.passWord,
            sex: data.sex,
            reply: data.reply ? data.reply : 0,   //   1    0
            releases: data.releases ? data.releases : 0,
          }
      }).catch(function (error) {
        console.log(error);
      });
      return;
    },
      //
      handleAvatarSuccess(res, file) {
        if(res.errcode === 0){
          this.$message({
            type: 'success',
            message: ""
          });
          this.imageUrl = this.$api + "/images/" + res.data;
          this.form.photoUrl = res.data;
        }else{
          this.$message({
            type: 'error',
            message: ""
          });
        }
        this.loading = false;
      },
      //
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg'|| 'image/png';
        const isLt2M = file.size / 1024 / 1024 < 2;
        console.log(file)
        if (!isJPG) {
          this.loading = false;
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.loading = false;
          this.$message.error(' 2MB!');
        }
        this.loading = true;
        return isJPG && isLt2M;
      },
      //
      handleAvatarError(res, file){
        this.loading = false;
        this.$message({
          type: 'error',
          message: ""
        });
      },
      //
      upgrade(){
        this.$confirm('VIP', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customers/vip', {customerId: this.$route.query.customerId})
            .then((response) => {
              console.log(response)
              this.$message({
                type: 'success',
                message: ""
              });
              history.go(-1)
            })
            .catch(function (error) {
              console.log(error);
            });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      ///
      disableFn(){
        let _this = this;
        let messageText = [];
        if (this.form.lockState == 0) {
          messageText[0] = '';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customers/lock', {customerId: this.$route.query.customerId})
            .then((response) => {
            console.log(response)
            this.$message({
              type: 'success',
              message: messageText[1]
            });
              history.go(-1)
          })
        .catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },
      //
      setReset(){
        this.$confirm("", '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/reset', {customerId: this.$route.query.customerId}).then((response) => {
            console.log(response)
            this.$message({
              type: 'success',
              message: ""
            });
            history.go(-1)
          }).catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },

      //
      prohibitReply(){
        let _this = this;
        let messageText = [];
        if (this.form.reply == 0) {
          messageText[0] = '?';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }
        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customer/reply', {customerId: this.$route.query.customerId})
            .then((response) => {
            this.$message({
              type: 'success',
              message: messageText[1]
            });
            history.go(-1)
          })
        .catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },

      //
      prohibitRelease(){
        let _this = this;
        let messageText = [];

        if (this.form.releases == 0) {
          messageText[0] = '?';
          messageText[1] = '';
        } else {
          messageText[0] = '';
          messageText[1] = '';
        }

        this.$confirm(messageText[0], '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.post('/api/back/customer/release', {customerId: this.$route.query.customerId})
            .then((response) => {
            console.log(response)
            this.$message({
              type: 'success',
              message: messageText[1]
            });
//            history.go(-1)
          })
        .catch(function (error) {
            console.log(error);
          });

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        })
      },

      //
      onSubmit() {
        let fromData = {
          customerId: this.$route.query.customerId,
          photoUrl: this.form.photoUrl,
          customerName: this.form.customerName,
          phone: this.form.phone,
          passWord: this.form.passWord,
          sex: this.form.sex,
          type: 0,
        }
        if(fromData.phone.length < 1){
          return
        }
        this.$axios.post('/api/back/customers', fromData)
          .then((response)=> {
        if (response.data.errcode == 0) {
          this.$message({
            showClose: true,
            type: "success",
            message: ''
          });
          history.go(-1)
        }
      })
      .catch(function (error) {
          console.log(error);
        });
      }
    },

  }
</script>

<style lang="scss">

</style>



